//Project Closing ----------- ------------ ---------
.ProjectPage-Closing {
    width: 100%;
    padding: 100px 0;
    padding-left: 10%;
    background-color: #000;
}

    .ProjectPage-ClosingText {
        font-size: 14px;
        font-weight: 300;

    }

    .ProjectPage-ClosingProjectTitle {
        font-size: 90px;
        font-weight: 900;
    }

        .ProjectPage-ClosingLink:hover {
            color: rgba(240, 240, 240, 0.5)
        }

@media screen and (max-width: 1000px){
    .ProjectPage-Closing {
        margin-top: 0;
    }
    .ProjectPage-ClosingProjectTitle {
        font-size: 12vw;
        margin: 1.3vw 0;
    }
}

